@import url('https://fonts.googleapis.com/css2?family=Rubik&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Blinker&display=swap');


.centeredLayout {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 80vh;
    margin: 0;
    flex-direction: column;
}
.container {
    text-align: center;
    padding: 40px;
    border-radius: 10px;
    width: auto;
    max-width: 500px;
    @media (max-width: 768px) {
        padding: 20px;
        max-width: 400px;
    }
}

.container2 {
    padding: 40px;
    border-radius: 10px;
    width: auto;
    max-width: 1200px;
    @media (max-width: 768px) {
        padding: 20px;
        max-width: 95%;
    }
}

.logoContainer {
    margin-bottom: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.logo {
    max-width: 300px;
    height: auto;
}
.logoSmall {
    max-width: 180px;
    height: auto;
    @media (max-width: 768px) {
        max-width: 150px;
        height: auto;
        align-self: center;
    }
}

.contactLink {
    padding: 8px 16px;
    margin: 10px;
    text-transform: none;

}

.socialLinks {
    display: flex;
    justify-content: center;
    gap: 30px;
    margin-top: 20px;

    .MuiLink-root {
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 54px;
        color: #333;
    }
}


.contactInfo {
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;

    .contactLink {
        display: flex;
        align-items: center;
        gap: 10px;
        margin: 5px 0;
        color: #007bff;
        text-decoration: none;

        &:hover {
            text-decoration: underline;
        }
    }

    .icon {
        font-size: 1.5rem;
    }
}

.spacing {
    margin-top: 3rem;
}